	module.exports = {"data":{"postsJson":{"title":"More Teaching on True Discipleship","questionsTitle":"FOLLOWING GODLY EXAMPLES","display":"block","bookName":"누가복음","ref":"16:1-17:10","html":"<p>   <strong>16</strong><u>1</u>또한 제자들에게 이르시되</p><h1>예수님</h1><h2>어떤 부자에게 청지기가 있는데 그가 주인의 소유를 낭비한다는 말이 그 주인에게 들린지라 <u>2</u>주인이 그를 불러 이르되 내가 네게 대하여 들은 이 말이 어찌 됨이냐 네가 보던 일을 셈하라 청지기 직무를 계속하지 못하리라 하니 <u>3</u>청지기가 속으로 이르되 주인이 내 직분을 빼앗으니 내가 무엇을 할까 땅을 파자니 힘이 없고 빌어 먹자니 부끄럽구나 <u>4</u>내가 할 일을 알았도다 이렇게 하면 직분을 빼앗긴 후에 사람들이 나를 자기 집으로 영접하리라 하고<br />   <u>5</u>주인에게 빚진 자를 일일이 불러다가 먼저 온 자에게 이르되 네가 내 주인에게 얼마나 빚졌느냐 <u>6</u>말하되 기름 백 말이니이다 이르되 여기 네 증서를 가지고 빨리 앉아 오십이라 쓰라 하고<br />   <u>7</u>또 다른 이에게 이르되 너는 얼마나 빚졌느냐 이르되 밀 백 석이니이다 이르되 여기 네 증서를 가지고 팔십이라 쓰라 하였는지라<br />   <u>8</u>주인이 이 옳지 않은 청지기가 일을 지혜 있게 하였으므로 칭찬하였으니 이 세대의 아들들이 자기 시대에 있어서는 빛의 아들들보다 더 지혜로움이니라 <u>9</u>내가 너희에게 말하노니 불의의 재물로 친구를 사귀라 그리하면 그 재물이 없어질 때에 그들이 너희를 영주할 처소로 영접하리라<br />   <u>10</u>지극히 작은 것에 충성된 자는 큰 것에도 충성되고 지극히 작은 것에 불의한 자는 큰 것에도 불의하니라 <u>11</u>너희가 만일 불의한 재물에도 충성하지 아니하면 누가 참된 것으로 너희에게 맡기겠느냐 <u>12</u>너희가 만일 남의 것에 충성하지 아니하면 누가 너희의 것을 너희에게 주겠느냐<br />   <u>13</u>집 하인이 두 주인을 섬길 수 없나니 혹 이를 미워하고 저를 사랑하거나 혹 이를 중히 여기고 저를 경히 여길 것임이니라 너희는 하나님과 재물을 겸하여 섬길 수 없느니라</h2><p><u>14</u>바리새인들은 돈을 좋아하는 자들이라 이 모든 것을 듣고 비웃거늘 <u>15</u>예수께서 이르시되</p><h1>예수님</h1><h2>너희는 사람 앞에서 스스로 옳다 하는 자들이나 너희 마음을 하나님께서 아시나니 사람 중에 높임을 받는 그것은 하나님 앞에 미움을 받는 것이니라<br />   <u>16</u>율법과 선지자는 요한의 때까지요 그 후부터는 하나님 나라의 복음이 전파되어 사람마다 그리로 침입하느니라 <u>17</u>그러나 율법의 한 획이 떨어짐보다 천지가 없어짐이 쉬우리라<br />   <u>18</u>무릇 자기 아내를 버리고 다른 데 장가 드는 자도 간음함이요 무릇 버림당한 여자에게 장가드는 자도 간음함이니라 <u>19</u>한 부자가 있어 자색 옷과 고운 베옷을 입고 날마다 호화롭게 즐기더라 <u>20</u>그런데 나사로라 이름하는 한 거지가 헌데 투성이로 그의 대문 앞에 버려진 채 <u>21</u>그 부자의 상에서 떨어지는 것으로 배불리려 하매 심지어 개들이 와서 그 헌데를 핥더라<br />   <u>22</u>이에 그 거지가 죽어 천사들에게 받들려 아브라함의 품에 들어가고 부자도 죽어 장사되매 <u>23</u>그가 음부에서 고통중에 눈을 들어 멀리 아브라함과 그의 품에 있는 나사로를 보고 <u>24</u>불러 이르되 아버지 아브라함이여 나를 긍휼히 여기사 나사로를 보내어 그 손가락 끝에 물을 찍어 내 혀를 서늘하게 하소서 내가 이 불꽃 가운데서 괴로워하나이다<br />   <u>25</u>아브라함이 이르되 얘 너는 살았을 때에 좋은 것을 받았고 나사로는 고난을 받았으니 이것을 기억하라 이제 그는 여기서 위로를 받고 너는 괴로움을 받느니라 <u>26</u>그뿐 아니라 너희와 우리 사이에 큰 구렁텅이가 놓여 있어 여기서 너희에게 건너가고자 하되 갈 수 없고 거기서 우리에게 건너올 수도 없게 하였느니라<br />   <u>27</u>이르되 그러면 아버지여 구하노니 나사로를 내 아버지의 집에 보내소서 <u>28</u>내 형제 다섯이 있으니 그들에게 증언하게 하여 그들로 이 고통 받는 곳에 오지 않게 하소서<br />   <u>29</u>아브라함이 이르되 그들에게 모세와 선지자들이 있으니 그들에게 들을지니라<br />   <u>30</u>이르되 그렇지 아니하니이다 아버지 아브라함이여 만일 죽은 자에게서 그들에게 가는 자가 있으면 회개하리이다<br />   <u>31</u>이르되 모세와 선지자들에게 듣지 아니하면 비록 죽은 자 가운데서 살아나는 자가 있을지라도 권함을 받지 아니하리라 하였다</h2><p>하시니라<br />   <strong>17</strong><u>1</u>예수께서 제자들에게 이르시되</p><h1>예수님</h1><h2>실족하게 하는 것이 없을 수는 없으나 그렇게 하게 하는 자에게는 화로다 <u>2</u>그가 이 작은 자 중의 하나를 실족하게 할진대 차라리 연자맷돌이 그 목에 매여 바다에 던져지는 것이 나으리라 <u>3</u>너희는 스스로 조심하라<br />   만일 네 형제가 죄를 범하거든 경고하고 회개하거든 용서하라 <u>4</u>만일 하루에 일곱 번이라도 네게 죄를 짓고 일곱 번 네게 돌아와 내가 회개하노라 하거든 너는 용서하라</h2>   하시더라<br />   <u>5</u>사도들이 주께 여짜오되</p><h1>제자들</h1><h3>우리에게믿음을더하소서</h3><p>하니 <u>6</u>주께서 이르시되</p><h1>예수님</h1><h2>너희에게 겨자씨 한 알만한 믿음이 있었더라면 이 뽕나무더러 뿌리가 뽑혀 바다에 심기어라 하였을 것이요 그것이 너희에게 순종하였으리라<br />   <u>7</u>너희 중 누구에게 밭을 갈거나 양을 치거나 하는 종이 있어 밭에서 돌아오면 그더러 곧 와 앉아서 먹으라 말할 자가 있느냐 <u>8</u>도리어 그더러 내 먹을 것을 준비하고 띠를 띠고 내가 먹고 마시는 동안에 수종들고 너는 그 후에 먹고 마시라 하지 않겠느냐 <u>9</u>명한 대로 하였다고 종에게 감사하겠느냐 <u>10</u>이와 같이 너희도 명령 받은 것을 다 행한 후에 이르기를 우리는 무익한 종이라 우리가 하여야 할 일을 한 것뿐이라 할지니라</h2>","path":"/334","Question1":"In this passage is there an action that you should follow? What is it?","Question2":"What decisions can you make this week to do this?","Question3":"In this passage is there an action from which you should repent? What is it?","Question4":"What decisions can you make this week to do this?","prevPath":"/333","nextPath":"/335"}},"pathContext":{}}

